export default {
	data() {
		return {
			goods:[],
			page:1,
			stopLoad:false,
			type:0,
			orderid:null,
			shop_id:null
		}
	},
	onLoad(options) {
		this.shop_id = uni.getStorageSync('shop_id')
		this.type = options.type
		this.getOtherGoods()
		this.orderid = options.orderid
	},
	methods: {
		toShop(){
			uni.redirectTo({
				url:'../index/index'
			})
		},
		toOrderDetail(){
			if(this.type==1){
				uni.redirectTo({
					url: "../orderDetail/orderDetail?orderid=" + this.orderid
				})
			}else if(this.type==2){
				uni.redirectTo({
					url: "../exchangeOrderDetail/exchangeOrderDetail?orderid=" + this.orderid
				})
			}
		},
		getOtherGoods() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Goods/othergoods',
				data: {
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.goods = res.data.data
						return
					}
					this.showToast(res.data.data)
				}
			})
		},
		toOtherGoods(goods_id, shop_id) {
			uni.redirectTo({
				url: "../goodsDetail/goodsDetail?id=" + goods_id + "&shop_id=" + shop_id
			})
		}
	}
}
